@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap);
html {
  scroll-behavior: smooth;
}

.App {
  position: relative;
  padding-bottom: 60px;
  min-height: calc(100vh - 60px);
}

body {
  margin: 0;
  font-family: 'Ubuntu', sans-serif !important;
}

.emphasize, .contact div a:hover, .message a:hover {
  text-shadow: 2px 2px 2px #ECFF00;
}

.about {
  padding: 50px 0;
  text-align: center;
}

.content {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}

.flex-row {
  display: flex;
  flex-flow: row nowrap;
}

.about-img {
  width: 100px;
  max-width: 10%;
  height: 100%;
  border-radius: 100%;
  margin: 0 30px;
}

.messages {
  width: 100%;
  text-align: left;
}

.message {
  border-left: 4px solid #00000066;
  padding-left: 20px;
  padding-right: 20px;
  margin: 15px 10px;
  text-align: justify;
}

.message:first-of-type {
  margin-top: 0;
}

.contact {
  justify-content: space-evenly;
  text-align: center;
}

.contact div a {
  color: black;
}

footer {
  position: absolute;
  width: 100%;
  bottom: 0;
  padding: 20px 0;
  background-color: rgb(18, 18, 18);
  color: white;
  font-family: 'Ubuntu Mono', monospace;
  font-size: 15px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}

footer > span:first-child {
  margin-left: 20px;
}

footer > span:last-child {
  margin-right: 20px;
}

.flag-gradient.en {
  background: linear-gradient(to right, #ffffff 0%, #0043d9 50%, #C8102E 60%, #ffffff 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
  font-weight: bold;
}

.flag-gradient.es {
  background: linear-gradient(to right, #EF3340 0%, #FFD100 50%, #EF3340 60%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
  font-weight: bold;
}

.button {
  color: white;
  text-decoration: none;
}

.button:hover {
  cursor: pointer;
  background-color: white;
  color: rgb(18, 18, 18);
}

.button-ico {
  margin-left: 10px;
}

.dancer {
  display: inline-block;
  -webkit-animation: dance 20s linear 0s infinite normal;
          animation: dance 20s linear 0s infinite normal;
}

@-webkit-keyframes dance {
  0% { 
      -webkit-transform:rotateY(0deg);
      -moz-transform:rotateY(0deg);
      -o-transform:rotateY(0deg);
      -ms-transform:rotateY(0deg);
   }
   100% {
      -webkit-transform:rotateY(1800deg);
      -moz-transform:rotateY(180deg);
      -o-transform:rotateY(1800deg);
      -ms-transform:rotateY(180deg);
   }
}

@keyframes dance {
  0% { 
      -webkit-transform:rotateY(0deg);
      -moz-transform:rotateY(0deg);
      -o-transform:rotateY(0deg);
      -ms-transform:rotateY(0deg);
   }
   100% {
      -webkit-transform:rotateY(1800deg);
      -moz-transform:rotateY(180deg);
      -o-transform:rotateY(1800deg);
      -ms-transform:rotateY(180deg);
   }
}

@media (max-width: 650px) {
  .about-img {
    margin: 0 auto;
    max-width: 25%;
    margin-bottom: 25px;
  }

  .about {
    padding: 25px 0;
    flex-flow: row wrap;
  }

  .contact {
    flex-flow: row wrap;
  }

  .contact div {
    width: 100%;
    margin-bottom: 25px;
  }

  footer {
    position: fixed;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
    padding-top: 10px;
  }

  footer > span, footer > a {
    margin: auto;
  }

  footer > span:first-child, footer > a:first-child {
    display: block;
    width: 100%;
    text-align: center;
    order: 3;
  }

  footer > span:not(:first-child), footer > a:not(:first-child) {
    text-align: center;
    width: 40%;
    margin: 0 10px 15px 10px;
  }

  .button-ico {
    display: block;
    margin-left: auto;
  }

  .flag-gradient {
    text-align: left;
  }
}
.nav {
    color: white;
    background-color: rgb(18, 18, 18);
    font-family: 'Ubuntu Mono', monospace;
    padding: 20px 0 30px 0;
}

.nav .content, .menu {
    justify-content: space-between;
}

.nav .content {
    height: 16px;
}

.menu {
    height: 16px;
}

.menu-choice {
    padding: 1px 5px;
    color: white;
    text-decoration: none;
    position: relative;
    margin: auto 20px;
    white-space: nowrap;
}

.menu-choice:hover, .menu-choice.active {
    background-color: white;
    color: rgb(18, 18, 18);
}

.menu-choice:hover {
    cursor: pointer;
}

.menu-choice.soon:hover {
    cursor: not-allowed;
}

.soon {
    color: red;
}

.soon::before {
    content: '.';
}

.soon-tag {
    position: absolute;
    top: 10px;
    right: -40px;
    display: inline-block;
    background-color: red;
    border-radius: 5px;
    padding: 2px;
    font-size: 10px;
    font-weight: bold;
    text-transform: uppercase;
    white-space:pre-wrap;
    max-width: 40px;
    text-align: center;
    color: white;
}

.menu-choice:hover > .soon-tag {
    -webkit-animation: wobble 1s infinite;
            animation: wobble 1s infinite;
}

@-webkit-keyframes wobble {
    from {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
    }
  
    15% {
      -webkit-transform: translate3d(-10%, 0, 0) rotate3d(0, 0, 1, -2.5deg);
              transform: translate3d(-10%, 0, 0) rotate3d(0, 0, 1, -2.5deg);
    }
  
    30% {
      -webkit-transform: translate3d(7%, 0, 0) rotate3d(0, 0, 1, 1.5deg);
              transform: translate3d(7%, 0, 0) rotate3d(0, 0, 1, 1.5deg);
    }
  
    45% {
      -webkit-transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1.5deg);
              transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1.5deg);
    }
  
    60% {
      -webkit-transform: translate3d(2.5%, 0, 0) rotate3d(0, 0, 1, 1deg);
              transform: translate3d(2.5%, 0, 0) rotate3d(0, 0, 1, 1deg);
    }
  
    75% {
      -webkit-transform: translate3d(-2.5%, 0, 0) rotate3d(0, 0, 1, -0.5deg);
              transform: translate3d(-2.5%, 0, 0) rotate3d(0, 0, 1, -0.5deg);
    }
  
    to {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
    }
}

@keyframes wobble {
    from {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
    }
  
    15% {
      -webkit-transform: translate3d(-10%, 0, 0) rotate3d(0, 0, 1, -2.5deg);
              transform: translate3d(-10%, 0, 0) rotate3d(0, 0, 1, -2.5deg);
    }
  
    30% {
      -webkit-transform: translate3d(7%, 0, 0) rotate3d(0, 0, 1, 1.5deg);
              transform: translate3d(7%, 0, 0) rotate3d(0, 0, 1, 1.5deg);
    }
  
    45% {
      -webkit-transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1.5deg);
              transform: translate3d(-5%, 0, 0) rotate3d(0, 0, 1, -1.5deg);
    }
  
    60% {
      -webkit-transform: translate3d(2.5%, 0, 0) rotate3d(0, 0, 1, 1deg);
              transform: translate3d(2.5%, 0, 0) rotate3d(0, 0, 1, 1deg);
    }
  
    75% {
      -webkit-transform: translate3d(-2.5%, 0, 0) rotate3d(0, 0, 1, -0.5deg);
              transform: translate3d(-2.5%, 0, 0) rotate3d(0, 0, 1, -0.5deg);
    }
  
    to {
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
    }
}

@media (max-width: 650px) {
    .nav .content {
        height: 60px;
        flex-flow: row wrap;
    }

    .nav {
        padding-bottom: 40px;
    }

    .menu {
        width: 100%;
        margin: 0  0 30px 0;
        order: -1;
        justify-content: space-evenly;
    }
}
.terminal-line {
    width: 100%;
    padding-left: 5px;
}

.terminal-crumb {
    position: relative;
    display: inline-block;
    height: 0;
    border-left: 8px solid black;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    padding: 0 8px;
}

.terminal-text {
    position: relative;
    top: -8.2px;
}

.master {
    color: rgb(46, 52, 47);
    background-color: rgb(78, 154, 6);
    border-left-color: rgb(52, 101, 164);
}

.master i {
    font-size: 0.8rem;
    margin-right: 7px;
}

.path {
    color: white;
    background-color: rgb(52, 101, 164);
    border-left-color: rgb(46, 52, 54);
}

.user {
    color: rgb(196, 160, 14);
    background-color: rgb(46, 52, 54);
}

.last {
    border-left-color: rgb(78, 154, 6);
    padding: 0;
}

.cursor {
    font-size: 12px;
    -webkit-animation: 1s blink step-end infinite;
    animation: 1s blink step-end infinite;
}

@-webkit-keyframes blink {
    from, to {
        color: transparent;
    }
    50% {
        color: inherit;
    }
}

@keyframes blink {
    from, to {
        color: transparent;
    }
    50% {
        color: inherit;
    }
}

@-webkit-keyframes "blink" {
    from, to {
        color: transparent;
    }
    50% {
        color: inherit;
    }
}

